
#about{
}
#headimg>img{
	/*background-color: #000;*/
	/*position: absolute;*/
	max-width: 18em;
	max-height: 18em;
	border-radius: 50%;
	margin: auto;
	margin-top: 8%;
}
#introduce{
	width: 100%;
	position: absolute;
	bottom: 0px;
	background-color: #fff;
	margin: auto;
	margin-top: 50px;
	padding: 4% 0%;
	left:0;
	text-align: left;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
#introduce>.left{
	width: 300px;
	margin: 0 10%;
}
#introduce>.right{
	margin-right: 10%;
/*	position: absolute;
	width: 50%;
	left: 40%;*/
}
.icon{
	margin-right: 10px;
}
@media screen and (max-width: 800px) {
#headimg>img{
	max-width: 15em;
	max-height: 15em;
	margin-top:5%;
}
}




